@use '../abstract' as a;

.player_table_container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.player_heading {
  margin: 2em 0;
}

.player_table {
  width: 70%;
  margin-bottom: 2em;
}

.player_table_header {
  display: grid;
  grid-template-columns: .5fr repeat(5, 1fr) .5fr .5fr;
  margin-bottom: 1em;
}

.player_table_header_cell {
  font-weight: 700;
}

.player_row {
  display: grid;
  grid-template-columns: .5fr repeat(5, 1fr) .5fr .5fr;

  a {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

